html,
body {
  margin: 0;
  background-color: #fff;
}

.main {
  width: 100%;

  bottom: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* padding: 0px 40px; */
  height: calc(100vh - 80px);
}

.st-button {
  position: fixed;
  bottom: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background-color: #466fff;
  width: 90%;
  height: 35px;
  border-radius: 10px;
}

.progressbar {
  height: 2px;
  

  background-color: #bbb;
}
